/* Variables CSS pour les couleurs, facilite les modifications */
:root {
  --primary-color: hsl(0, 72%, 47%); /* Bleu principal */
  --secondary-color: hsl(195, 97%, 47%); /* Vert d'eau secondaire */
  --background-color: hsl(174, 24%, 68%); /* Arrière-plan très clair - Ajuste ici pour changer le fond principal */
  --card-background: hsl(0, 5%, 81%); /* Fond des cartes blanc */
  --text-color-dark: #333; /* Texte foncé */
  --text-color-light: #fff; /* Texte clair */
  --shadow-color: rgba(0, 0, 0, 0.1); /* Couleur d'ombre douce */
  --border-color: hsl(0, 0%, 19%); /* Couleur de bordure générale */
  --text-color-medium: #555; /* Couleur de texte intermédiaire */
}

/* Styles généraux conservés ou adaptés */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color-dark);
  line-height: 1.6;
  overflow-x: hidden; /* Empêche le défilement horizontal */
}

/* --- NOUVEAUX STYLES POUR LA PAGE D'ACCUEIL (HOME) --- */
/* Ces styles s'appliquent uniquement à la structure spécifique de la page d'accueil */

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Section Entête Profil */
.profile-header-section {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  color: var(--text-color-light);
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-shadow: 0 4px 8px var(--shadow-color);
}

.profile-picture-large {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid var(--text-color-light);
  margin-bottom: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.profile-header-section h1 {
  margin: 0;
  font-size: 2.5em;
  font-weight: 700;
  letter-spacing: 1.5px;
}

.fancy-name {
    font-weight: 300;
    opacity: 0.9;
}

.profile-subtitle {
  margin-top: 5px;
  font-size: 1.2em;
  opacity: 0.9;
}

/* Section Cartes de Navigation */
.navigation-cards-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
  max-width: 1200px;
  margin: 20px auto;
  flex-grow: 1;
}

/* Style des NOUVELLES cartes de navigation */
.new-card {
  background-color: var(--card-background);
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-shadow: 0 2px 5px var(--shadow-color);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.new-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px var(--shadow-color);
}

.new-card-icon {
  font-size: 3em;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.new-card-label {
  margin: 0;
  font-size: 1.1em;
  font-weight: 600;
  color: var(--text-color-dark);
}

/* Section Bouton d'Installation */
.install-button-section {
  padding: 20px;
  text-align: center;
}

.install-app-btn {
  background-color: var(--secondary-color);
  color: var(--text-color-dark);
  border: none;
  border-radius: 5px;
  padding: 12px 25px;
  font-size: 1.1em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-weight: 600;
  box-shadow: 0 2px 5px var(--shadow-color);
}

.install-app-btn:hover {
  background-color: #42c8ab;
  transform: translateY(-2px);
}

/* Media query pour ajuster l'affichage de l'ENTETE PROFIL sur les grands écrans */
@media (min-width: 768px) {
  .profile-header-section {
    flex-direction: row;
    justify-content: center;
    gap: 40px;
    text-align: left;
  }

  .profile-text-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
  }

   .profile-picture-large {
      margin-bottom: 0;
   }
}

/* --- STYLES POUR LES PAGES SPÉCIFIQUES (Profil, Formation, Projets, etc.) --- */
/* Ces styles ciblent les éléments *à l'intérieur* des composants de page */

.page {
  padding: 20px;
  max-width: 900px;
  margin: 20px auto;
  background-color: var(--card-background);
  border-radius: 10px;
  box-shadow: 0 2px 8px var(--shadow-color);
  /* Ajout de styles pour la mise en page intérieure Flexbox/Grid */
  display: flex; /* Permet de contrôler la direction des enfants directs (h2, profile-content-container) */
  flex-direction: column; /* Les enfants sont empilés par défaut */
  gap: 20px; /* Espace entre les sections (titre, contenu profil, etc.) */
}

.page h2 {
  color: var(--primary-color);
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px; /* Ajusté la marge pour mieux s'intégrer au gap du parent */
  font-size: 2em;
  font-weight: 600;
}

/* Conteneur Flexbox/Grid pour l'image et le texte du profil */
.profile-content-container {
    display: flex;
    flex-direction: column; /* Image et texte empilés par défaut sur mobile */
    align-items: center; /* Centre les éléments quand ils sont empilés */
    gap: 20px; /* Espace entre l'image et le texte */
}


/* Style de l'image spécifique à la page Profil */
.profile-image-page { /* Utilise la nouvelle classe de l'image */
    width: 180px; /* Taille de l'image */
    height: 180px;
    border-radius: 50%; /* Rendre l'image ronde si tu préfères */
    /* Ou border-radius: 10px; si tu préfères un carré arrondi comme sur la home */
    object-fit: cover;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); /* Ombre pour la faire ressortir */
    /* margin-bottom: 20px; */ /* Le gap du parent gère l'espace */
}

/* Style du conteneur de texte du profil */
.profile-text-content-page {
    flex-grow: 1; /* Permet au bloc de texte de prendre l'espace restant en mode ligne */
    /* text-align: center; */ /* Alignement centré par défaut sur mobile */
}

/* Style du nom sur la page Profil */
.profile-name-page {
    font-size: 1.4em; /* Taille plus grande pour le nom */
    font-weight: bold;
    color: var(--text-color-dark);
    margin-top: 0;
    margin-bottom: 10px;
    text-align: center; /* Centré sur mobile */
}

/* Style de la description sur la page Profil */
.profile-description-page { /* Utilise aussi la classe 'section' si elle a des styles globaux */
    font-size: 1em;
    color: var(--text-color-medium); /* Utilise une couleur intermédiaire pour la description */
    line-height: 1.8; /* Plus d'espace entre les lignes */
    margin-bottom: 0;
    text-align: center; /* Centré sur mobile */
}


/* Styles des cartes *spécifiques* aux pages (comme dans Education, Projets) */
/* Reste inchangé par rapport à la dernière version pour ne pas casser Education */
.page .card {
  background-color: #f9f9f9;
  border-left: 5px solid var(--primary-color);
  margin: 15px 0;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
  text-align: left;
  position: relative;
  overflow: hidden;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.page .card:hover {
    transform: translateX(5px);
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.page .card h3 {
    margin-top: 0;
    margin-bottom: 5px;
    color: var(--text-color-dark);
    font-size: 1.2em;
    font-weight: 600;
}

.page .card p {
    margin-bottom: 10px;
    color: var(--text-color-medium); /* Utilise la variable intermédiaire */
    font-size: 1em;
}

.page .card .year {
  position: absolute;
  right: 20px;
  top: 15px;
  font-weight: bold;
  color: #777;
  font-size: 0.95em;
  background-color: #eee;
  padding: 3px 8px;
  border-radius: 5px;
}

.page .skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid var(--border-color);
}

.page .chip {
  background-color: var(--secondary-color);
  color: var(--text-color-dark);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  /* border: 1px solid darken(var(--secondary-color), 5%); */ /* Cette syntaxe darken n'est pas standard CSS, à utiliser avec un préprocesseur comme Sass */
}

/* Optionnel: Styles pour les listes (ul/li) si tu les utilises dans d'autres pages */
.page ul {
    padding-left: 20px;
}
.page li {
    margin-bottom: 10px;
}

/* --- Media query pour la mise en page latérale sur grands écrans --- */
@media (min-width: 768px) {
    /* Entête Home (déjà fait) */
    .profile-header-section {
      flex-direction: row; justify-content: center; gap: 40px; text-align: left;
    }
    .profile-text-content {
        display: flex; flex-direction: column; justify-content: center;
    }
     .profile-picture-large { margin-bottom: 0; }

    /* Mise en page latérale pour la page Profil */
    .page .profile-content-container {
        flex-direction: row; /* Passe en ligne sur grand écran */
        align-items: flex-start; /* Aligne les éléments en haut */
        text-align: left; /* Alignement du texte à gauche */
    }

    .page .profile-image-page {
        margin-bottom: 0; /* Retire la marge basse en mode ligne */
        margin-right: 30px; /* Ajoute de la marge à droite de l'image pour l'espace avec le texte */
        /* Ajuster la taille de l'image pour le desktop si désiré */
        /* width: 200px; height: 200px; */
    }

    .page .profile-name-page,
    .page .profile-description-page {
        text-align: left; /* Texte aligné à gauche sur grand écran */
    }

}